@import url('https://fonts.googleapis.com/css2?family=Philosopher&display=swap');

body {
    --twitter-color: #50b7f5;
    --twitter-background: #e6ecf0;
  }
  *{
      margin: 0px;
      padding: 0px;
      box-sizing: border-box;
  }
  .app {
    display: flex;
    height: 100vh;
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 10px;
    position: relative;
  }
  .image{
    padding:10px;
    border-radius: 30px;
    background-color: #F2F2F2;
    margin:10px;
    border-bottom: lightgrey 2px solid;
    cursor: pointer;
    transition: all .8s;
    
  }
  .image:hover{
    background-color: #e8f5fe;
    width:500px !important;
    height:600px!important;
    
  }
  .name{
    text-align: center;
    font-size: 24px;
    text-transform: capitalize;
    font-family: 'Philosopher', sans-serif;
  }
  .feed{
    flex:0.3;
    display: flex;
    flex-direction: column;
    margin-left: 270px;
    margin-top: 20px;
  }
  .form{
    display:flex;
    align-items: center;
    justify-content: center;
    background-color:black;
    padding: 10px;
    border-radius: 12px;
    color: white;
    text-transform: capitalize;
    font-family: 'Philosopher', sans-serif;
  }
  .input_file{
    width:0px;
    height:0px;
  }

  label{
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid white;
    margin: 10px auto;
    color: var(--twitter-background);
    cursor: pointer;
  }

  label > .MuiSvgIcon-root {
    font-size: 30px;
  }